import CustomButton from "components/CustomButton";
import CustomCheckBox from "components/CustomCheckBox";
import CustomForm from "components/CustomForm";
import CustomTable from "components/CustomTable";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import {
    deleteDocuments,
    getCsInUse,
    LOGIN,
    profileVerification,
    saveProfileVerification,
    uploadDocument,
    uploadDocuments,
} from "redux/Actions/Auth";
import {
    ERROR_MESSAGES,
    INPUT_TYPES,
    LABEL,
    PROFILE_TABLE,
    SERVICES,
    SNACK_MESSAGES,
    SNACK_VARIANT,
    STRINGS,
} from "shared/Constants";
import {
    COMPUTER_SYSTEMS_FAMILIAR_DATA,
    createCsValue,
    createProfileVerification,
    createUploadPayload,
    DOCUMENTS_UPLOAD_DATA,
    getComputerSystemsData,
    getDefaultDateString,
    getDeleteValueDocuments,
    getInititalValueDocuments,
    getLocalizedDateString,
    getTableData,
    profileDefaultValues,
    profileVerificationData,
    PROFILE_VERIFICATION_DATA,
} from "views/authenticated/constants";
import "./styles.scss";
import useNotification from "hooks/Notifications";
import { Icon } from "@mui/material";
import { Images } from "shared/Images";
import CustomTextInput from "components/CustomTextInput";
import { getDocsUrl } from "shared/Utilities";
import DocumentViewerModal from "components/DocumentViewer";
function ProfileVerification({
    csInUse = [],
    verificationData = {},
    setVerificationData = () => {},
}) {
    const dispatch = useDispatch();
    console.log(csInUse, verificationData, "verification data");
    const [computerSystems, setComputerSystems] = useState(
        getComputerSystemsData(csInUse, verificationData.cs_in_use),
    );
    const [tableData, setTableData] = useState(getTableData(verificationData.documents));
    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [regNumber,setRegNumber] = useState(verificationData?.reg_number)
    const [validTill,setValidTill] = useState(getDefaultDateString(verificationData?.valid_till) || '') 
    const [errorMessage, setErrorMessage] = useState({
        reg_number: "",
        valid_till: "",
    });
    // const [defaultValues, setDefaultValues] = useState({
    //     ...verificationData,
    //     ...profileDefaultValues(
    //         verificationData?.reg_number,
    //         getDefaultDateString(verificationData?.valid_till),
    //     ),
    // });
    const [msg, sendNotifications] = useNotification();

    useEffect(() => {
        // setDefaultValues(
        //     profileDefaultValues(verificationData?.reg_number, verificationData?.valid_till),
        // );
        // getComputerSystemsData(csInUse, verificationData.cs_in_use);
        // setTableData(getTableData(verificationData.documents));
        // setDefaultValues({ ...verificationData });
    }, []);
    useEffect(() => {
        // setDefaultValues(
        //     profileDefaultValues(verificationData?.reg_number, verificationData?.valid_till),
        // );
        getComputerSystemsData(csInUse, verificationData.cs_in_use);
    }, [csInUse]);
    const onSubmit = () => {
        if(!regNumber){
            setErrorMessage(prev => {
                return {
                    ...prev,
                    reg_number:ERROR_MESSAGES.REGISTRATION_NO
                }
            })
            return;
        }
        if(validTill && !tableData[1].uploadedFile){
            sendNotifications({
                msg:ERROR_MESSAGES.INSURANCE_DOCUMENT,
                variant:SNACK_VARIANT.ERROR
            })
        }else{
            setVerificationData((prev) => {
                return {
                    ...prev,
                    valid_till:validTill,
                    reg_number:regNumber,
                    cs_in_use: [...createCsValue(computerSystems)],
                };
            });
            const payload = createProfileVerification({reg_number:regNumber,valid_till:validTill}, computerSystems);
            dispatch(
                saveProfileVerification(
                    payload,
                    (response) => {
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }
        
    };
    const onCheckboxSelect = (event) => {
        const itemIndex = computerSystems.findIndex((item, index) => {
            return item.id == event.target.id;
        });
        // console.log(computerSystems, itemIndex, event.target.id, event.target.checked);
        const newArr = [...computerSystems];
        newArr[itemIndex].value = event.target.checked;
        setComputerSystems(newArr);
    };
    const handleUpload = (event) => {
        const payload = createUploadPayload(event.target.files[0], event.target.id);
        dispatch(
            uploadDocuments(
                payload,
                (response) => {
                    const tempArr = [...tableData];
                    const tempData = tempArr[event.target.id];
                    tempData.uploadedFile = response.data.document;
                    tempData.docId = response.data.id;
                    tempData.document_type = response.data.document_type;
                    tempArr[event.target.id] = tempData;
                    setVerificationData((prev) => {
                        return {
                            ...prev,
                            documents: [...getInititalValueDocuments(tempArr)],
                        };
                    });
                    setTableData(tempArr);
                    event.target.value = null;
                },
                (error) => {
                    sendNotifications({
                        msg: error,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onAddServiceClick = () => {
        setTableData((prev) => [
            ...prev,
            {
                id: prev.length + 1,
                title: SERVICES.OTHER,
                uploadedFile: null,
                document_type: 4,
            },
        ]);
    };
    const onDeleteClick = (event) => {
        dispatch(
            deleteDocuments(
                { id: tableData[event.target.id].docId },
                (response) => {
                    const tempArr = [...tableData];
                    const tempData = tempArr[event.target.id];
                    tempData.uploadedFile = null;
                    tempArr[event.target.id] = tempData;
                    setVerificationData((prev) => {
                        return {
                            ...prev,
                            documents: [...getInititalValueDocuments(tempArr)],
                        };
                    });
                    setTableData(tempArr);
                },
                (error) => {
                    sendNotifications({
                        msg: error,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onRowClick = (index) => {
        if (tableData[index].uploadedFile) {
            setDocumentUrl(getDocsUrl(tableData[index].uploadedFile));
            setViewDoc(true);
        }
    }
    return (
        <div className="tab_content">
            {/* <CustomForm
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={profileVerificationData(false)}
                renderSubmitButton={false}
                defaultValues={{ ...defaultValues }}
            /> */}
            <div className="row">
            <div className="col-md-6 mb-4">
                    <CustomTextInput
                        id={"reg_number"}
                        // iconStyle={"iconStyle"}
                        type={INPUT_TYPES.NUMBER}
                        errorMessage={errorMessage.reg_number}
                        value={regNumber}
                        onChange={(event)=>{
                            if(!event.target.value){
                                setErrorMessage(prev=>{
                                    return {
                                        ...prev,
                                        reg_number: ERROR_MESSAGES.REGISTRATION_NO
                                    }
                                })
                            }else{
                             setErrorMessage(prev =>{
                                return {
                                    ...prev,
                                    reg_number: ''
                                }
                             })   
                            }
                            setRegNumber(event.target.value);
                        }}
                        titleText={LABEL.REGISTRATION_NO}
                        placeholder={STRINGS.ENTER_HERE}
                        // onChange={onChange}
                    />
                </div>
                <div className="col-md-6 mb-4">
                    <CustomTextInput
                        id={"valid_till"}
                        // icon={!showPassword ? Images.ShowPassword : Images.HidePassword}
                        // iconStyle={"iconStyle"}
                        titleText={LABEL.VALID_INSURANCE}
                        type={INPUT_TYPES.DATE}
                        value={validTill}
                        onChange={(event)=>{
                            // if(event.target.value){
                                setValidTill(event.target.value);
                            // }
                        }}
                        // errorMessage={errorMessage.valid_till}
                        // type={showPassword ? STRINGS.PASSWORD_ID : INPUT_TYPES.TEXT}
                        placeholder={STRINGS.ENTER_PASSWORD}

                        // onIconClick={onIconClick}
                    />
                </div>
                </div>
            <div className="row">
                <div className="col-12 mb-3">
                    <label className="mb-3">{LABEL.COMPUTER_SYSTEMS}</label>
                    <div className="row">
                        <CustomCheckBox
                            checkBoxData={computerSystems}
                            onCheckboxSelect={onCheckboxSelect}
                        />
                    </div>
                </div>
                <div className="col-12">
                    <div className="d-flex align-items-center justify-content-between mb-2">
                        <label className="mb-0">{LABEL.PROFILE_DOCUMENTS}</label>
                        <CustomButton
                            title={STRINGS.ADD_MORE_SERVICES}
                            onClick={onAddServiceClick}
                            className="btn btn-sm btn-outline-secondary add_btn"
                            Icon={Images.PlusIcon}
                        />
                    </div>
                </div>
                <div className="col-12">
                    <div className="table-responsive">
                        <CustomTable
                            data={tableData}
                            activeTab={STRINGS.PROFILE_TABLE}
                            handleUpload={handleUpload}
                            onClick = {onRowClick}
                            onDeleteClick={onDeleteClick}
                        />
                    </div>
                    {viewDoc && <DocumentViewerModal viewDoc={viewDoc} documentUrl={documentUrl} setViewDoc={setViewDoc}/>}
                </div>
                <div className="col-12 text-center mt-3">
                    <CustomButton
                        title={STRINGS.SUBMIT}
                        onClick={onSubmit}
                        className="btn btn-md btn-primary"
                    />
                </div>
            </div>
        </div>
    );
}

export default ProfileVerification;
